
import Divider from 'vux/src/components/divider/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import Scroll from 'assembly/scroll/scroll'
import { mapActions } from 'vuex'
import localStore from 'util/localStore'
import Loading from 'assembly/loading/loading'

const PAGESIZE = 10

export default {
data () {
return {
keyword: '',
switchIndex: 0,
storageCart: {},
isSearch: false, // 是否显示结果tab栏
promotion: [], // 促销产品列表
productList: [], // 常规产品列表
zeroList: [], // 零配件列表
cNum: 0, // 促销产品结果数
num: 0, // 常规产品结果数
lNum: 0, // 零配件结果数
cPageNo: 1, // 促销产品当前页数
pageNo: 1, // 常规产品当前页数
lPageNo: 1, // 零配件当前页数
cHasMore: true, // 促销产品是否有下一页
hasMore: true, // 常规产品是否有下一页
lHasMore: true, // 零配件是否有下一页
cInited: false,
inited: false,
lInited: false,
listenScroll: true,
pFirstOrderFlag: false, // 是否下过首订
pFirstOrderDate: '', // 首订是否通过
pCitorganlevel: '', // 分销商/专卖店


popupIndex: '', // 弹框促销id
popupContent: {}, // 弹框促销详情
promotionOneTotal: 0, // 单个首订礼包总价
ifPop: false, // 促销详情弹框
ifBtn: true, // 促销详情弹框是否显示确定按钮，如果主产品都缺货则不显示
ifOrderPlan: false 
}
},
computed: {
dot () {
return this.$store.state.dot
}
},
methods: {
numberClick (e) {
switch (e.target.nodeName) {
case 'path':
if (e.target.parentNode.parentNode.className.search('vux-number-selector-plus vux-number-disabled') >= 0) {
this.$vux.toast.text('配额不足')
}
break
case 'svg':
if (e.target.parentNode.className.search('vux-number-selector-plus vux-number-disabled') >= 0) {
this.$vux.toast.text('配额不足')
}
break
case 'a':
if (e.target.className.search('vux-number-selector-plus vux-number-disabled') >= 0) {
this.$vux.toast.text('配额不足')
}
break
}
},
changePromotionNum (index) {
if (this.popupContent.promotionRuleVO.productList[index].mqq && this.popupContent.promotionRuleVO.productList[index].mqq > this.popupContent.promotionRuleVO.productList[index].shopCartNum) {
this.popupContent.promotionRuleVO.productList[index].shopCartNum = 0
}
},
goBack () {
this.$router.go(-1)
},
switchTabItem (index) {
this.switchIndex = index
this.$refs.scroll.scrollTo(0, 0)
},
toCart () {
this.$router.push({
path: '/shoppingCart'
})
},
toSearch () {
if (!this.keyword) {
this.$vux.toast.text('请输入产品名称或产品编号')
return
}
if (this.cInited || this.inited || this.lInited) {
this.$vux.toast.text('拼命请求中，请稍候')
return
}
this.storageCart = localStore.get('TUP_cart')
this.cPageNo = 1
this.pageNo = 1
this.lPageNo = 1
this.cHasMore = true
this.hasMore = true
this.lHasMore = true
this.promotion = []
this.productList = []
this.zeroList = []
this._getPromotion()
this._getProducts()
this._getZero()
this.isSearch = true
},
loadMore () {
if ((this.switchIndex === 0 && !this.cHasMore) || (this.switchIndex === 1 && !this.hasMore) || (this.switchIndex === 2 && !this.lHasMore)) {
return
}
if (this.switchIndex === 0) {
this.cPageNo ++
this._getPromotion()
}
if (this.switchIndex === 1) {
this.pageNo ++
this._getProducts()
}
if (this.switchIndex === 2) {
this.lPageNo ++
this._getZero()
}
},
judgePermiss () {
if (this.pCitorganlevel === '5') {
this.$vux.toast.text('分销商不允许下单')
return false
}
if (this.pFirstOrderFlag === true && this.pFirstOrderDate === null) {
this.$vux.toast.text('首订订单正在审核中，审核通过再来订货吧~')
return false
}








return true
},
changeCartNum (i, j) {
if (!this.judgePermiss()) {
return
}
if (this.pFirstOrderFlag === false) {
this.$vux.toast.text('首订不能订购常规产品哦~')
return
}
if (this.ifOrderPlan) { // 如果已处于下草稿时间内
if ((this.switchIndex === 1 && !j && this.productList[i].availableQty === 0) || (this.switchIndex === 2 && !j && this.zeroList[i].availableQty === 0)) {
this.$vux.toast.text('配额不足')
return
}
if (!j) { // 加入购物车
if (this.switchIndex === 1) {
this.productList[i].shopCartNum = this.productList[i].mqq ? this.productList[i].mqq : 1 
} else if (this.switchIndex === 2) {
this.zeroList[i].shopCartNum = this.zeroList[i].mqq ? this.zeroList[i].mqq : 1 
}
return
} else { // 改变购物车数量(当购物车从0变为1的时候，会先执行if再执行else)
if (this.switchIndex === 1 && this.productList[i].mqq && this.productList[i].shopCartNum < this.productList[i].mqq) {
this.productList[i].shopCartNum = 0
} else if (this.switchIndex === 2 && this.zeroList[i].mqq && this.zeroList[i].shopCartNum < this.zeroList[i].mqq) {
this.zeroList[i].shopCartNum = 0
}
this.setProductCart({
item: j
})
}
this.setDot()
} else { // 如果不处于
this.$tupHttp(this, 'front/order/order/checkOrderPlan', 'POST', {
bookType: this.pFirstOrderFlag ? 0 : 1, // 0普订1首订
timeType: 0 
}).then(res => {
if (res.data.model) { // 请求结果为处于
this.ifOrderPlan = true
if ((this.switchIndex === 1 && !j && this.productList[i].availableQty === 0) || (this.switchIndex === 2 && !j && this.zeroList[i].availableQty === 0)) {
this.$vux.toast.text('配额不足')
return
}
if (!j) { // 加入购物车
if (this.switchIndex === 1) {
this.productList[i].shopCartNum = this.productList[i].mqq ? this.productList[i].mqq : 1 
} else if (this.switchIndex === 2) {
this.zeroList[i].shopCartNum = this.zeroList[i].mqq ? this.zeroList[i].mqq : 1 
}
} else { // 改变购物车数量(当购物车从0变为1的时候，会先执行if再执行else)
this.setProductCart({
item: j
})
}
this.setDot()
} else {
this.$vux.toast.text('当前时间不允许添加到购物车')
}
})
}
},
showPop (i, j) {
if (!this.judgePermiss()) {
return
}
if (this.ifOrderPlan) { // 如果已处于下草稿时间内
this.popupContent = j
this.popupIndex = i
this.ifBtn = !this.popupContent.promotionRuleVO.productList.every(i => {
return !i.isHave
}) // 如果主产品都缺货，隐藏弹框的确定按钮
if (this.popupContent.type === 4) {
this.promotionOneTotal = 0
for (let z = 0; z < this.popupContent.promotionRuleVO.productList.length; z++) {
this.promotionOneTotal += this.popupContent.promotionRuleVO.productList[z].productNum * this.popupContent.promotionRuleVO.productList[z].orderRatioPrice
this.popupContent.promotionRuleVO.productList[z].shopCartNum = this.popupContent.promotionRuleVO.productList[z].productNum
}

this.ifBtn = this.popupContent.promotionRuleVO.productList.every(i => {
return i.isHave
})
}
this.ifPop = true
} else { // 如果不处于
this.$tupHttp(this, 'front/order/order/checkOrderPlan', 'POST', {
bookType: this.pFirstOrderFlag ? 0 : 1, // 0普订1首订
timeType: 0 
}).then(res => {
if (res.data.model) { // 请求结果为处于
this.ifOrderPlan = true
this.popupContent = j
this.popupIndex = i
this.ifBtn = !this.popupContent.promotionRuleVO.productList.every(i => {
return !i.isHave
})
if (this.popupContent.type === 4) {
this.promotionOneTotal = 0
for (let z = 0; z < this.popupContent.promotionRuleVO.productList.length; z++) {
this.promotionOneTotal += this.popupContent.promotionRuleVO.productList[z].productNum * this.popupContent.promotionRuleVO.productList[z].orderRatioPrice
this.popupContent.promotionRuleVO.productList[z].shopCartNum = this.popupContent.promotionRuleVO.productList[z].productNum
}

this.ifBtn = this.popupContent.promotionRuleVO.productList.every(i => {
return i.isHave
})
}
this.ifPop = true
} else {
this.$vux.toast.text('当前时间不允许添加到购物车')
}
})
}
},
closePop () {
this.ifPop = false
},
confirm () {
let ifValid = this.popupContent.promotionRuleVO.productList.some(item => {
return item.shopCartNum > 0
}) // 是否增加了主产品，true加入或更新购物车，false不添加或移除购物车
if (ifValid) {
let sum = 0
this.popupContent.promotionRuleVO.productList.forEach((value, index) => {
sum += value.shopCartNum
})
this.promotion[this.popupIndex].shopCartNum = sum
} else {
this.promotion[this.popupIndex].shopCartNum = 0
}
this.ifPop = false
this.setPromotionCart({
item: this.popupContent
})
this.setDot()
},
_getProducts () {
this.inited = true
this.$tupHttp(this, 'front/product/selectProductsByClientIdFromSolr?search=' + this.keyword + '&pageNo=' + this.pageNo + '&pageSize=' + PAGESIZE).then(res => {
this.inited = false
let productList = res.data.models.map(item => {

let storageProductCart = this.storageCart.product
for (let i = 0, len = storageProductCart.length; i < len; i++) {
if (storageProductCart[i].pCode === item.pCode) {
item.shopCartNum = storageProductCart[i].shopCartNum
item.selected = storageProductCart[i].selected || false
return item
}
}
item.selected = true
item.shopCartNum = 0
return item
})
this.productList = this.productList.concat(productList)
this.num = res.data.pageInfo.total
if (res.data.pageInfo && res.data.pageInfo.total <= this.pageNo * PAGESIZE) { // 是否有下一页
this.hasMore = false
}
if (res.data.models.length) {
setTimeout(() => {
this.$refs.scroll.refresh()
}, 500)
}
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_getPromotion () {
this.cInited = true
this.$tupHttp(this, 'front/product/selectPromotionsByClientIdFromSolr?search=' + this.keyword + '&pageNo=' + this.cPageNo + '&pageSize=' + PAGESIZE).then(res => {
this.cInited = false
let promotion = res.data.models.map(item => {
let storagePromotionCart = this.storageCart.promotion
for (let i = 0, len = storagePromotionCart.length; i < len; i++) {
if (storagePromotionCart[i].id === item.id) {
let sum = 0
storagePromotionCart[i].promotionRuleVO.productList.forEach((value, index) => {
if (value.isHave) {
sum += value.shopCartNum
}
})
item.shopCartNum = sum
item.promotionRuleVO.productList.map(childItem => {
for (let j = 0; j < storagePromotionCart[i].promotionRuleVO.productList.length; j++) {
if (childItem.pCode === storagePromotionCart[i].promotionRuleVO.productList[j].pCode) {
childItem.shopCartNum = storagePromotionCart[i].promotionRuleVO.productList[j].shopCartNum
}
}
return childItem
})
return item
}
}
item.shopCartNum = 0
return item
})
this.promotion = this.promotion.concat(promotion)
this.cNum = res.data.pageInfo.total
if (res.data.pageInfo && res.data.pageInfo.total <= this.cPageNo * PAGESIZE) { // 一次加载，促销不做分页
this.cHasMore = false
}
if (res.data.models.length) {
setTimeout(() => {
this.$refs.scroll.refresh()
}, 500)
}
}, () => {
this.cInited = false
}).catch(() => {
this.cInited = false
})
},
_getZero () {
this.lInited = true
this.$tupHttp(this, 'front/product/selectProductsByClientIdFromSolr?catalogCode=08&isParts=1&search=' + this.keyword + '&pageNo=' + this.lPageNo + '&pageSize=' + PAGESIZE).then(res => {
this.lInited = false
let storageProductCart = localStore.get('TUP_cart').product
let zeroList = res.data.models.map(item => {

for (let i = 0, len = storageProductCart.length; i < len; i++) {
if (storageProductCart[i].pCode === item.pCode) {
item.shopCartNum = storageProductCart[i].shopCartNum
item.selected = storageProductCart[i].selected || false
return item
}
}
item.selected = true
item.shopCartNum = 0
return item
})
this.zeroList = this.zeroList.concat(zeroList)
this.lNum = res.data.pageInfo.total
if (res.data.pageInfo && res.data.pageInfo.total <= this.lPageNo * PAGESIZE) { // 是否有下一页
this.lHasMore = false
}
if (res.data.models.length) {
setTimeout(() => {
this.$refs.scroll.refresh()
}, 50)
}
}, () => {
this.lInited = false
}).catch(() => {
this.lInited = false
})
},
...mapActions([
'setDot',
'setPromotionCart',
'setProductCart'
])
},
created () {
this.pFirstOrderFlag = localStore.get('TUP_pFirstOrderFlag')
this.pFirstOrderDate = localStore.get('TUP_pFirstorderDate')
this.pCitorganlevel = localStore.get('TUP_pCitorganlevel')
},
components: {
Divider,
XNumber,
XButton,
Scroll,
Tab,
TabItem,
Loading,
Popup,
Badge
}
}



